.paginator {
  display: flex;
  padding-top: 30px;
  -webkit-user-select: none;
  user-select: none;
  font-size: 20px;
  cursor: pointer;
  justify-content: flex-end;
    button:disabled {
      background-color: #cccccc;
      color: #666666;
    }
    > button {
      background: rgba(23, 162, 184, 0.25);
    }
    button:hover {
      background: rgba(39, 184, 23, 0.56);
      font-weight: bold;
      color: white;
    }
    .active {
      font-weight: bold;
      color: white;
      background: rgba(39, 184, 23, 0.56);
    }
  > span {
    padding-right: 3%;
  }
  .paginatorItems {
    display: flex;
    justify-content: space-between;
    max-width: 18%;
    margin-right: auto;
    align-items: center;
  }
}
